'use strict'

export default function(router) {
	router.map({
		'/': {
			name: 'home',
			component: (resolve) => {
				require(['./views/index/app.vue'], resolve)
			},
			subRoutes: {
				//商品展示
				'/': {
					name: 'goods-show',
					component: (resolve) => {
						require(['./views/index/sub/goods.vue'], resolve)
					},
				},
				//搜索
				'/search/:key': {
					name: 'search',
					component: (resolve) => {
						require(['./views/index/sub/search.vue'], resolve)
					},
					subRoutes: {
						'/': {
							name: 'search-time',
							component: (resolve) => {
								require(['./views/index/sub/sub/time.vue'], resolve)
							},
						},
						'/hot': {
							name: 'search-hot',
							component: (resolve) => {
								require(['./views/index/sub/sub/hot.vue'], resolve)
							},
						},
						'/price': {
							name: 'search-price',
							component: (resolve) => {
								require(['./views/index/sub/sub/price.vue'], resolve)
							},
						}
					}
				}
			}
		},
		//发布
		'/publish': {
			name: 'publish',
			component: (resolve) => {
				require(['./views/publish/app.vue'], resolve)
			}
		},
		//商品详情
		'/detail/:id': {
			name: 'detail',
			component: (resolve) => {
				require(['./views/goodDetail/app.vue'], resolve)
			}
		},
		//登录
		'/login': {
			name: 'login',
			component: (resolve) => {
				require(['./views/validates/login.vue'], resolve)
			},
		},
		//注册
		'/register': {
			name: 'register',
			component: (resolve) => {
				require(['./views/validates/register.vue'], resolve)
			},
		},
		// 意见反馈
		'/advice': {
			name: 'advice',
			component: (resolve) => {
				require(['./views/validates/advice.vue'], resolve)
			}
		},
		// 修改密码
		'/pwd': {
			name: 'pwd',
			component: (resolve) => {
				require(['./views/validates/pwd.vue'], resolve)
			}
		},
		// 验证身份
		'/numberauth': {
			name: 'numberauth',
			component: (resolve) => {
				require(['./views/validates/numberauth.vue'], resolve)
			}
		},
		//关于
		'/about': {
			name: 'about',
			component: (resolve) => {
				require(['./views/about/app.vue'], resolve)
			}
		},
		//联系我们
		'/contact': {
			name: 'contact',
			component: (resolve) => {
				require(['./views/contact/app.vue'], resolve)
			}
		},
		// 个人中心
		'/user': {
			name: 'user',
			component: (resolve) => {
				require(['./views/user/app.vue'], resolve)
			},
			subRoutes: {
				'/': {
					name: 'user-data',
					component: (resolve) => {
						require(['./views/user/sub/data.vue'], resolve)
					},
				},
				'/publish': {
					name: 'user-publish',
					component: (resolve) => {
						require(['./views/user/sub/publish.vue'], resolve)
					},
				},
				'/favorite': {
					name: 'user-favorite',
					component: (resolve) => {
						require(['./views/user/sub/favorite.vue'], resolve)
					},
				},
				'/msg': {
					name: 'user-msg',
					component: (resolve) => {
						require(['./views/user/sub/msg.vue'], resolve)
					},
				}
			}
		},
		// 协议
		'/agreement': {
			name: 'agreement',
			component: (resolve) => {
				require(['./views/agreement/app.vue'], resolve)
			},
		}
	})
}


